* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
  line-height: 1.6;
  color: #222;
}

@media (min-width: 360px) {
  html {
    font-size: 18px;
  }
}

@media (min-width: 460px) {
  html {
    font-size: 20px;
  }
}

@media (min-width: 768px) {
  html {
    font-size: 24px;
  }
}

@media (min-width: 1280px) {
  html {
    font-size: 28px;
  }
}
/* ========================================================================== */
/* MAIN STYLES */
/* ========================================================================== */


div.app {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    max-width: 1600px;
    margin: 0 auto;
}

main {
    flex-grow: 1;
    background-color: #fafafa;
    text-align: center;
}

section {
    padding: 30px 10px;
}

section span {
    text-transform: uppercase;
}

section form > *, section div.result h2 {
    margin-bottom: 15px;
}

@media (min-width: 768px) {
    section {
        padding: 50px 20px;
    }

    section form > *, section div.result h2 {
        margin-bottom: 20px;
    }
}



/* ========================================================================== */
/* FORM STYLES */
/* ========================================================================== */


section form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

section form input, section form button {
    padding: 5px;
    border: 2px solid #999;
    background-color: #fff;
    font-size: 1rem;
}

section form input {
    width: 200px;
}

section form button {
    text-transform: uppercase;
    cursor: pointer;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

div.bankAccount input:first-child {
    width: 35px;
    margin-right: 5px;
    text-align: center;
}

div.bankAccount input:last-child {
    width: 260px;
}

@media (min-width: 360px) {
    div.bankAccount input:last-child {
        width: 290px;
    }
}

@media (min-width: 460px) {
    section form input {
        width: 12em;
        padding: 10px;
    }

    section form button {
        padding: 10px 20px;
    }

    div.bankAccount input:first-child {
        width: calc(2em + 10px);
        margin-right: 10px;
    }

    div.bankAccount input:last-child {
        width: 18em;
    }
}

@media (min-width: 768px) {
    section form {
        margin-bottom: 30px;
    }
}

@media (min-width: 1024px) {
    section form button:hover {
        background-color: #222;
        color: #fff;
    }
}



/* ========================================================================== */
/* RESULT STYLES */
/* ========================================================================== */


section div.result p {
    margin-bottom: 10px;
}

section h2 {
    font-size: 1.2rem;
    text-transform: uppercase;
}

section span.incorrect {
    color: red;
}

section span.correct {
    color: green;
}

header {
    width: 100%;
    margin-top: 52px;
    background-image: url(/check-number/static/media/bgheader.78633fe1.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

header h1 {
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    font-size: 22px;
    text-align: center;
    line-height: 1.8;
}

header h1 span {
    text-transform: uppercase;
}

@media (min-width: 360px) {
    header h1 {
        padding: 20px;
        font-size: 24px;
    }
}

@media (min-width: 460px) {
    header h1 {
        font-size: 28px;
    }
}

@media (min-width: 640px) {
    header {
        height: 150px;
    }

    header h1 {
        padding: 0;
        line-height: 150px;
    }
}

@media (min-height: 460px) and (min-width: 640px) {
    header {
        margin-top: 60px;
    }
}

@media (min-width: 768px) {
    header {
        margin-top: 0;
    }

    header h1 {
        font-size: 32px;
    }
}

@media (min-width: 1024px) {
    header {
        height: 200px;
    }

    header h1 {
        font-size: 40px;
        line-height: 200px;
    }
}

@media (min-width: 1280px) {
    header {
        height: 250px;
    }

    header h1 {
        font-size: 54px;
        line-height: 250px;
    }
}





nav {
    position: fixed;
    top: -270px;
    left: 0;
    right: 0;
    background-color: #eee;
    text-align: center;
    font-size: 20px;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

nav.activeMenu {
    top: 0;
}

nav ul {
    display: flex;
    flex-direction: column;
    list-style: none;
}

nav ul li {
    margin: 0 30px;
}

nav ul li a {
    display: block;
    padding: 10px 0;
    border-bottom: 2px solid #888;
    color: #222;
    text-decoration: none;
    text-transform: uppercase;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

nav div {
    padding: 10px 0;
    color: #222;
}

@media (min-width: 460px) {
    nav ul li {
        margin: 0 60px;
    }
}

@media (min-height: 460px) and (min-width: 640px) {
    nav {
        top: -310px;
        font-size: 25px;
    }
}

@media (min-width: 768px) {
    nav {
        position: static;
        font-size: 16px;
        line-height: 1;
    }

    nav ul {
        flex-direction: row;
        justify-content: center;
    }

    nav ul li {
        margin: 0;
    }

    nav ul li a {
        margin: 15px 0;
        padding: 0px 20px;
        border-bottom: none;
        border-right: 2px solid #888;
    }

    nav ul li a:hover {
        color: royalblue;
    }

    nav ul li a.active {
        color: cadetblue;
    }

    nav ul li:last-child a {
        border-right: none;
    }

    nav div {
        display: none;
    }
}

@media (min-width: 1024px) {
    nav {
        font-size: 20px;
    }

    nav ul li a {
        margin: 20px 0;
    }
}

@media (min-width: 1280px) {
    nav {
        font-size: 22px;
    }

    nav ul li a {
        padding: 0px 30px;
    }
}
footer {
    padding: 10px 0;
    background-color: #bbb;
    color: #000;
    text-align: center;
    font-size: 12px;
}

footer span {
    font-style: italic;
}

@media (min-width: 360px) {
    footer {
        font-size: 14px;
    }
}

@media (min-width: 768px) {
    footer {
        font-size: 18px;
    }
}

